import React, { useState, useEffect } from "react";
import { useGlobalContext } from "../Global/GlobalContext";

const SelectTag = () => {
  const {
    songshan,
    xinyi,
    daan,
    zhongshan,
    zhongzheng,
    datong,
    wanhua,
    wenshan,
    nangang,
    neihu,
    shilin,
    beitou,
    handleSelect,
    finishSelectingData,
    selecting,
    selectingData,
  } = useGlobalContext();

  const [selectState, setSelectState] = useState([]);

  const handleSelecting = () => {
    return new Promise((res, rej) => {
      if (!selecting) {
        res();
      }
      rej("Loading...");
    });
  };

  const chgSelectValue = () => {
    if (!selecting) {
      handleSelecting()
        .then(() => selectingData())
        .then(() => handleSelect(JSON.parse(selectState)))
        .then(() => finishSelectingData())
        .catch((err) => console.log(err));
    }
  };

  useEffect(() => {
    //Change global selected state while selected value changed
    chgSelectValue();
  }, [selectState]);

  return (
    <div className="absolute left-[5%] top-[21%] sm:top-20 sm:left-[28%] sm:w-80 w-11/12 flex justify-center items-center ">
      {!selecting && (
        <>
          <p className="text-2xl font-bold"></p>
          <select
            className="dropdown-arrow"
            value={selectState}
            onChange={(e) => setSelectState(e.target.value)}
          >
            <option value="" disabled selected hidden>
              --
            </option>
            <option value={JSON.stringify(songshan)}></option>
            <option value={JSON.stringify(xinyi)}></option>
            <option value={JSON.stringify(daan)}></option>
            <option value={JSON.stringify(zhongshan)}></option>
            <option value={JSON.stringify(zhongzheng)}></option>
            <option value={JSON.stringify(datong)}></option>
            <option value={JSON.stringify(wanhua)}></option>
            <option value={JSON.stringify(wenshan)}></option>
            <option value={JSON.stringify(nangang)}></option>
            <option value={JSON.stringify(neihu)}></option>
            <option value={JSON.stringify(shilin)}></option>
            <option value={JSON.stringify(beitou)}></option>
          </select>
        </>
      )}
    </div>
  );
};

export default SelectTag;
